import React, { Component } from 'react';
import Datetime from 'react-datetime';

require('./App.css');

class App extends Component {
	constructor(props) {
		super(props);
		this.state = {
			title: '',
			desc: '',
			details: '',
			loc: '',
			startTime: '',
			endTime: ''
		};

		this.handleSubmit = this.handleSubmit.bind(this);
		this.changeTitle = this.changeTitle.bind(this);
		this.changeDesc = this.changeDesc.bind(this);
		this.changeDetails = this.changeDetails.bind(this);
		this.changeLoc = this.changeLoc.bind(this);
	}

	handleSubmit(event) {
		console.log(this.state);
		event.preventDefault();
		const request = new FormData();
		request.append('file', this.fileInput.files[0]);
		request.append('picture', this.fileInput.files[0].name);
		request.append('title', this.state.title);
		request.append('description', this.state.desc);
		request.append('details', this.state.details);
		request.append('loc', this.state.loc);
		request.append('startTime', this.state.startTime);
		request.append('endTime', this.state.endTime);

		console.log('event', event);
		console.log(`Selected file - ${this.fileInput.files[0].name}`);
	}

	handleChange(event) {
		this.setState({ [event.target.name]: event.target.value });
	}

	render() {
		return (
			<div className="app">
				<h1>Admin Panel</h1>
				<h2>Create a new event:</h2>
				<form onSubmit={this.handleSubmit}>
					<label htmlFor="titleInput">
						Enter Title:
						<input
							id="titleInput"
							type="text"
							value={this.state.title}
							onChange={this.handleChange}
						/>
					</label>
					<br />
					<label htmlFor="descriptionInput">
						Enter Description:
						<input
							id="titleInput"
							type="text"
							value={this.state.desc}
							onChange={this.handleChange}
						/>
					</label>
					<br />
					<label htmlFor="locationInput">
						Enter Location:
						<input
							id="locationInput"
							type="text"
							value={this.state.loc}
							onChange={this.handleChange}
						/>
					</label>
					<br />
					<label htmlFor="detailInput">
						Enter Details:
						<textarea
							id="detailInput"
							value={this.state.details}
							onChange={this.handleChange}
						/>
					</label>
					<br />
					<label htmlFor="fileInput">
						Upload Picture:
						<input
							id="fileInput"
							type="file"
							ref={input => {
								this.fileInput = input;
							}}
						/>
					</label>
					<br />
					<label htmlFor="startTime">
						Start Time:
						<Datetime id="startTime" value={this.state.startTime} />
					</label>
					<br />
					<label htmlFor="endTime">
						End Time:
						<Datetime id="endTime" value={this.state.endTime} />
					</label>
					<br />
					<button type="submit">Submit</button>
				</form>
			</div>
		);
	}
}

export default App;
